.title {
  text-align: center;
  cursor: default;
  margin-bottom: 20px;
}

.products__list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.products__title {
  cursor: default;
}

.products__item {
  border-radius: 10%;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 15px #0003;
}

.products__image {
  margin: 10px 10px 10px 20px;
  transition: transform .3s;
}

.products__image:hover {
  transform: scale(1.05);
}

.thumb {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.products__text {
  cursor: default;
}

.products__subtext {
  background-color: #bae6e6;
  border-radius: 10%;
  padding: 0 10px;
  font-size: 32px;
  transition: transform .3s;
}

.products__subtext:hover, .products__subtext:focus {
  cursor: pointer;
  transform: scale(1.05);
}
